.App > div {

}

.todolist {
  height: max-content;
  border: 3px solid #003B46;
  border-radius: 5px;
  padding: 20px;
  background-color: #66A5AD ;
  margin: 30px 10px;
}

.task {
  font-weight: bold;
}

.task-done {
  text-decoration: line-through;
  opacity: 0.5;
}

